<template>
  <div>
    <nav aria-label="Page navigation example">
      <ul class="pagination">
        <li class="page-item" :class="{ disabled: !pages.has_pre }">
          <a class="page-link" @click="$emit('get-product', pages.current_page - 1)" aria-label="Previous">
            <span aria-hidden="true">&laquo;</span>
          </a>
        </li>
        <li
          class="page-item"
          :class="{ active: page === pages.current_page }"
          v-for="(page, idx) in pages.total_pages"
          :key="idx"
        >
          <a class="page-link" @click="$emit('get-product', page)">{{ page }}</a>
        </li>
        <li class="page-item" :class="{ disabled: !pages.has_next }">
          <a class="page-link" @click="$emit('get-product', pages.current_page + 1)" aria-label="Next">
            <span aria-hidden="true">&raquo;</span>
          </a>
        </li>
      </ul>
    </nav>
  </div>
</template>

<script>
export default {
  name: "Pagenation",
  data() {
    return {};
  },
  props: {
    pages: {
      type: Object,
      default() {
        return {};
      },
    },
  },
};
</script>
